import React from 'react';
import './Answer.css';


//Functional Component
const Answer = (props) => {

    let answer = Object.keys(props.answer)
        .map((qAnswer, i) => (
            <li
                className=
                {
                    props.correctAnswer === qAnswer ?
                        'correct' :
                        props.clickAnswer === qAnswer ?
                            'incorrect' : ''
                }

                onClick={() => props.CheckAnswer(qAnswer)}
                key={qAnswer}>
                {props.answer[qAnswer]}

            </li>

        ));

    return (
        <>
            <ul disabled={props.clickAnswer ? true : false} className="Answers">
                {answer}
            </ul>
            <div>
                {
                    // IF statment for correct answer
                    props.correctAnswer ?
                    'Correct Answer' :
                    props.clickAnswer ?
                    'Incorect Answer' : ''
                }
            </div>

        </>
    );
}
export default Answer;