import React, { component, Component } from 'react';
import Question from './question/Question';
import Answer from './answer/Answer';
import './TestCenter.css';
import { Link } from "react-router-dom";

export default class Test extends Component {
    
    state = {
        questions: {
            1: ' Which of the following is more accurate for the following declaration? x = Circle()',
            2: ' How to create a frame in Python?',
            3: ' Pylab is a package that combine _______,________&______ into a single namespace.',
            4: ' We can create an image in canvas. Select the option to do so?',
            5: 'What is output for following code => y = [4, 5,1j] y.sort()',
        },
        answers: {
            1: {

                1: 'x contains a reference to a Circle object',
                2: 'x actually contains an object of type Circle',
                3: 'Now you can assign int value to x',
            },
            2: {

                1: 'Frame = frame.new()',
                2: 'Frame = Frame()',
                3: 'Frame = new.window()',
            },
            3: {

                1: ' Numpy, scipy & pandas',
                2: 'Numpy, pandas & matplotlib',
                3: 'Numpy, scipy & matplotlib',
            },

            4: {

                1: ' Canvas.create_image(filename)',
                2: ' Image = Photoimage(file=imagefilename)',
                3: ' Image = PhotoImage(imagefilename)',
            },

            
            5: {

                1: ' Type Error',
                2: ' [1j,4,5]',
                3: ' [5,4,1j]',
            },


        },
        correctAnswers: {
            1: '1',
            2: '2',
            3: '3',
            4: '2',
            5: '1',
        },
        correctAnswer: 0,
        clickAnswer: 0,
        step: 1,
        score: 0,
    }

    CheckAnswer = answer => {
        const { correctAnswers, correctAnswer, clickAnswer, step, score } = this.state;
        if (answer === correctAnswers[step]) {
            this.setState({
                score: score + 1,
                correctAnswer: correctAnswers[step],
                clickAnswer: answer
            });
        }
        else {
            this.setState({
                correctAnswer: 0,
                clickAnswer: answer
            });


        }
    }

    nextStep = step => {
        this.setState({
            step: step + 1,
            correctAnswer: 0,
            clickAnswer: 0,

        });
    }

    render() {
        let { questions, answers, correctAnswer, clickAnswer, step, score} = this.state;
        return (
            <div className='Content'>
               
        <Link to="/deploy1/testpage">Back to TestPage</Link>

                {step <= Object.keys(questions).length ?
                    (<>
                    <Question
                        question={questions[step]}
                    />
                    <Answer
                        answer={answers[step]}
                        step={step}
                        CheckAnswer={this.CheckAnswer}
                        correctAnswer={correctAnswer}
                        clickAnswer={clickAnswer}

                    />
                    <button
                        className='NextStep'
                        disabled={
                            clickAnswer && Object.keys(questions).length >= step
                                ? false : true
                        }
                        onClick={() => this.nextStep(step)}
                    >
                        Next
                </button>
                </>) : (
                    <div className="finalpage">
                        <h1>you have complete the quiz</h1>
                        <p> your score is: {score} of {Object.keys(questions).length}</p>
                        <p>Thank you!</p>
                        </div>
                )
                }
                
            </div>

        );

    }
}