import React from "react";
import { Link } from "react-router-dom";

const UserPage = () => {
return(
    <div>
         <Link to="/deploy1">Back to The Main Page</Link>
        <ul>
            {["Alex, Eric, Sarah, Zeinab, Farjam"].map((user, idx) => {
                return <li key={idx}>{user}</li>;
            })}
        </ul>
    </div>
);
};
export default UserPage;
